import StorieContainer from './StorieContainer';
import SideContent from './SideContent';
import CardContent from './CardContent';

const Content = ()=>{

    const cardList = [];

    for (let i = 0; i < 6; i++){
        cardList.push(<CardContent index={i} />);
    }

    return (
        <div className="content bd-grid">
            <div className="content-left">
                <StorieContainer/>
                {cardList}
            </div>
            <SideContent/>
        </div>
    );
}

export default Content;