import Carousel from "react-elastic-carousel";
import AllIMG from "../util/AllImg.js";

const StorieContainer = ()=> {

    const BREAK_POINTS = [
        { width: 1, itemsToShow: 4 },
        { width: 300, itemsToShow: 4 },
        { width: 395, itemsToShow: 7 },
    ];

    const story__wrapper = [];
    for (let i = 0; i < 15; i++){
        story__wrapper.push(
            <div className="story-wrapper" key={i}> 
                <div className="story-inner">
                    <img className="story-img" src={AllIMG[i]} alt=""/>
                    <div className="gradient"></div>
                </div>
                <h2>Outfits ‚úå</h2>
            </div>);
    }

    return (
       <div className="stories_container">
            <div className="stories-container-inner">
            <Carousel breakPoints={BREAK_POINTS} >
                {story__wrapper}
            </Carousel>
            </div>
        </div>
    );
};

export default StorieContainer;