import instagram_logo from '../img/Instagram Logo.png';
import perfil_img from '../img/BeansAvatar.jpg';
import PopUpPerfil from './PopUpPerfil';
import {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';

const Navbar = ()=> {
    const [isOpenPopUp, setIsOpenPopUp] = useState(false);

    const handleOpenPopUp = () =>{
        setIsOpenPopUp(!isOpenPopUp);
    }

    const closePopUp = () => {
        setIsOpenPopUp(false);
    }

    document.querySelector('body').addEventListener('click', closePopUp);
    
    return (
        <header className="l-header">
            <nav className="nav bd-grid">
                <div className="nav-left">
                    <Link to="/" className="logo">
                        <img 
                            src={instagram_logo} 
                            alt="Instagram Logo"
                        />

                    </Link>
                </div>
                <div className="nav-middle input-container">
                    <input type="text" placeholder="Pesquisar"/>
                    <i className='bx bx-search search-input'></i>
                </div>
                <div className="nav-right">
                    <Link href="#" className="nav-link home-link">
                        {
                            isOpenPopUp ? 
                            <i className='bx bx-home-alt'></i> : 
                            <i className='bx bxs-home'></i>
                        }
                        
                    </Link>
                    <Link href="#" className="nav-link messenger-link">
                        <i className='bx bxl-messenger' ></i>
                        <span className="messenger-notification-number">3</span>
                    </Link>
                    <Link href="#" className="nav-link explore-link">
                        <i className='bx bx-compass' ></i>
                    </Link>
                    <Link href="#" className="nav-link activity-lin">
                        <i className='bx bx-heart'></i>
                    </Link>
                    <div className="perfil__container" onClick={e => e.stopPropagation()}>
                        <span className="toggle-perfil" onClick={handleOpenPopUp}>
                            <img className={isOpenPopUp ? "active":""} src={perfil_img}/>
                        </span>
                        {isOpenPopUp ? <PopUpPerfil/> : null}
                    </div>

                </div>
            </nav>
        </header>
    );
}

export default Navbar;