import perfil_img from '../img/BeansAvatar.jpg';
import { Link } from 'react-router-dom';
import AllIMG from "../util/AllImg.js";
const CardContent = (props)=>{
    return(
        <div className="card-content">
            <div className="card-content__top">
                <div className="card-content__top__left">
                    <img src={perfil_img} alt=""/>
                    <div className="right-content-info">
                        <Link href="#" className="user-name">beans</Link>
                        <Link href="#" className="user-info-h">√Åudio original</Link>
                    </div>
                </div>
                <span className="toggle-modal-info"><i className='bx bx-dots-horizontal-rounded'></i></span>
            </div>
            <div className="card-content-img">
                <img src={AllIMG[props.index]} alt=""/>
            </div>
            <div className="card-content__footer">
                <div className="card-content__footer-top">
                    <div className="card-content__footer-top__left">
                        <span className="favorite"><i className='bx bx-heart' ></i></span>
                        <span className="comment"><i className='bx bx-comment'></i></span>
                        <span className="share"><i className='bx bx-navigation'></i></span>
                    </div>
                    <span className="marker"><i className='bx bx-bookmark'></i></span>
                </div>
                <div className="card-content__footer-body">
                    <span>fuar.moda üî•üî•</span>
                    <span className="expand-content">...<span className="expand-content-text">mais</span></span>
                    <span className="published-day">H√° 2 DIAS</span>
                </div>
                <div className="card-content__footer-footer">
                    <span className="emoji-container"><i className='bx bx-happy'></i></span>
                    <textarea className="comment-content"></textarea>
                    <span className="btn-send-comment">Publicar</span>
                </div>
            </div>
        </div>
    );
};

export default CardContent;